<template>
  <div class="product-detail layout">
    <detail-top :data="detailData"></detail-top>
    <div class="detail-content clearfix">
      <div class="leftDetail">
        <detail-introduce></detail-introduce>
      </div>
      <div ref="rightDetail" id="rightDetail" class="rightDetail" v-sticky="{top:0}">
        <detail-calculation v-if="token"></detail-calculation>
        <detail-applicant v-else></detail-applicant>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import detailTop from '@/components/product/detail/TheDetailTop'
import detailIntroduce from '@/components/product/detail/TheDetailIntroduce'
import detailCalculation from '@/components/product/detail/TheDetailCalculation'
import detailApplicant from '@/components/product/detail/TheDetailApplicant'
import Sticky from '@/assets/plugins/sticky.js'
import { getLocalStorage } from '@/assets/js/localStorage'

export default {
  name: 'TheDetailMain',
  data () {
    return {
      detailData: {},
      searchBarFixed: false,
      memberId: ''
    }
  },
  mounted () {
    if (getLocalStorage('userInfo')) {
      this.memberId = getLocalStorage('userInfo').id
    }
    this.getDetail()
  },
  methods: {
    getDetail () {
      this.GetDetailList({
        productId: this.$route.query.productId,
        memberId: this.memberId
      })
    },
    ...mapActions(['GetDetailList'])
  },
  computed: {
    ...mapGetters([
      'token'
    ])
  },
  directives: {
    Sticky
  },
  // destroyed () { // 离开该页面需要移除这个监听的事件
  //   window.removeEventListener('scroll', this.handleScroll)
  // },
  components: { detailTop, detailIntroduce, detailCalculation, detailApplicant }
}
</script>

<style scoped lang="scss">
  .leftDetail {
    margin-bottom: 92px;
    float: left;
  }

  .rightDetail {
    margin-bottom: 20px;
    float: right;
    &.isFixed {
      position: fixed;
      top: 10px;
    }
  }
</style>
